@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}
/* navbar styles */
.navbar_navbar__286Vz {
  width: 100%;
  background: #effaf0;
  padding: 20px 10px;
  box-sizing: border-box;
}
.navbar_navbar__286Vz ul {
  display: flex;
  margin: 0 auto;
  max-width: 960px;
  align-items: center;
}
.navbar_title__1gCHU {
  margin-right: auto;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 1.2em;
}
.navbar_navbar__286Vz button,
.navbar_navbar__286Vz a {
  margin-left: 16px;
}
.navbar_navbar__286Vz a {
  color: #333;
  text-decoration: none;
}

.Home_container__1s89M {
  display: grid;
